<template>
  <div class="container">
    <el-row>
      <el-col :span="21">
        <el-input v-model="search" placeholder="搜索玩家（名称/备注/标签）"></el-input>
      </el-col>
      <el-col :span="3">
        <el-select v-model="selectedSeason" placeholder="选择赛季">
          <el-option
            v-for="season in seasons"
            :key="season"
            :label="season === '' ? '' : season"
            :value="season"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-table :data="filteredData" border :default-sort="{prop: 'Global Rank', order: sortOrder}">
      <el-table-column label="赛季" prop="Season" width="110" sortable></el-table-column>
      <el-table-column label="地区" prop="Region" width="100"></el-table-column>
      <el-table-column label="全球排名" prop="Global Rank" width="110" sortable></el-table-column>
      <el-table-column label="玩家名称" prop="Player Name" width="200">
        <template v-slot="scope">
          <div>
            {{ scope.row["Player Name"] }}
            <span v-if="scope.row.Remarks !== ''"> ({{ scope.row.Remarks }})</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="玩家标签" prop="Player Tag" width="120"></el-table-column>
      <el-table-column label="卡组信息" prop="Deck Info">
        <template v-slot="scope">
          <div class="card-images-container" v-if="scope.row['Deck Info']">
            <img
              v-for="card in scope.row['Deck Info'].split(',')"
              :key="card"
              :src="cardImages[card]"
              alt="card"
              class="card-image"
            />
          </div>
          <div v-else>No Deck Info Available</div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { cardImages } from "@/cardImages";
export default {
  data() {
    return {
      search: "",
      selectedSeason: "",
      seasons: [],
      cardImages: cardImages,
      data: [],
      sortOrder: 'ascending', // Initial sort order
    };
  },
  computed: {
    filteredData() {
      return this.data.filter((item) => {
        const includesSearch =
          item["Player Name"].toLowerCase().includes(this.search.toLowerCase()) ||
          item.Remarks.toLowerCase().includes(this.search.toLowerCase()) ||
          item["Player Tag"].toLowerCase().includes(this.search.toLowerCase());

        const matchesSeason =
          this.selectedSeason === "" || item["Season"] === this.selectedSeason;

        return includesSearch && matchesSeason;
      });
    },
  },
  mounted() {
    this.loadData();
  },
  methods: {
    loadData() {
      import("@/assets/cncr_data.json").then((json) => {
        this.data = json.data;
        this.seasons = this.extractSeasons(json.data);
        // 在 seasons 中添加清空赛季的选项
        this.seasons.unshift("");
      });
    },
    extractSeasons(data) {
      // 从数据中提取赛季
      return Array.from(new Set(data.map((item) => item.Season)));
    },
  },
};
</script>

<style>
.container {
  padding: 100px;
}

.card-images-container {
  display: flex;
}

.card-image {
  margin-right: 5px;
  width: 64px;
  height: 70px;
}
</style>
